* {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

.wrap {
  max-width: 1179px;
  margin: auto;
}

.header {
  min-height: 630px;
  background: url(../img/header_bg.jpg) center / cover;
}

.header .nav {
  background: #fff;
  padding: 22px 0;
}

.header .wrap {
  display: flex;
  justify-content: space-between;
  max-width: 1200px;
}

.header .left,
.header .right {
  display: flex;
  align-items: center;
}

.header .bars {
  font-size: 26px;
}

.header .logo {
  margin: 2px 0 0 63px;
}

.header .phone {
  background: #ffd200;
  padding: 6px 32px;
  border-radius: 6px;
}

.header .phone span {
  border-bottom: 1px dotted #000;
}

.header .logout {
  margin: 2px 0 0 63px;
}

.section_info {
  padding: 50px 0;
}

.section_info .wrap {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.section_info .box {
  text-align: center;
}

.section_info .box .icon {
  border: 5px solid #ffd200;
  border-radius: 50%;
  width: 130px;
  height: 130px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 25px;
}

.section_info .box .link {
  font-size: 24px;
  text-transform: capitalize;
}